/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

#homepage {
    background-color: #f4f7fc;
}

.homeHalf {
    float: left;
    height: 95vh;
}

#homeLogo {
    background-color: #1b2c3a;
    padding: 23vh 0 0 14vw;
    width: 44%;
}

#homeLogoIcon {
    width: 20vw;
    opacity: 0;
    -webkit-animation: rotateLogo 4s ease-out .5s forwards;
            animation: rotateLogo 4s ease-out .5s forwards;
}

@-webkit-keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

@keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

#homeSlant {
    position: absolute;
    top: 0;
    left: 32vw;
    background-color: #1b2c3a;
    width: 23vw;
    height: 95vh;
    -webkit-animation: slantyX 4s ease-out .5s forwards;
            animation: slantyX 4s ease-out .5s forwards;
}

@-webkit-keyframes slantyX {
    100% {
        -webkit-transform: skewX(-20deg);
                transform: skewX(-20deg);
    }
}

@keyframes slantyX {
    100% {
        -webkit-transform: skewX(-20deg);
                transform: skewX(-20deg);
    }
}

#homeWords {
    width: 56%;
    padding: 37vh 0 0 15vw;
    opacity: 0;
    -webkit-animation: wordAppear 4s .5s forwards;
            animation: wordAppear 4s .5s forwards;
}

@-webkit-keyframes wordAppear {
    from {
        opacity: 0;
        -webkit-transform: translateX(-20vw);
                transform: translateX(-20vw);
    }
    to {
        opacity: 1;
        -webkit-transform: translateX(0vw);
                transform: translateX(0vw);
    }
}

@keyframes wordAppear {
    from {
        opacity: 0;
        -webkit-transform: translateX(-20vw);
                transform: translateX(-20vw);
    }
    to {
        opacity: 1;
        -webkit-transform: translateX(0vw);
                transform: translateX(0vw);
    }
}

